@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Press Start 2P', 'Noto Sans KR', cursive;
  -webkit-font-smoothing: none;
  -moz-osx-font-smoothing: grayscale;
  image-rendering: pixelated;
}

.App {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  transition: background 0.3s ease;
}

/* 다마고치 스타일 배경 - 도트 패턴 */
.App.dark-mode {
  background: 
    repeating-linear-gradient(
      0deg,
      #1a1a2e 0px,
      #1a1a2e 2px,
      #16213e 2px,
      #16213e 4px
    );
}

.App.light-mode {
  background: 
    repeating-linear-gradient(
      0deg,
      #e0f7fa 0px,
      #e0f7fa 2px,
      #b2ebf2 2px,
      #b2ebf2 4px
    );
}

.App.timer-active.dark-mode {
  background: #0f3460;
}

.App.timer-active.light-mode {
  background: #b2ebf2;
}

.container {
  max-width: 800px;
  width: 100%;
}

/* 픽셀 헤더 */
.header {
  text-align: center;
  color: #00ff41;
  margin-bottom: 30px;
  position: relative;
  text-shadow: 3px 3px 0px #000;
}

.light-mode .header {
  color: #00796b;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.3);
}

.header h1 {
  font-size: 2em;
  margin-bottom: 15px;
  letter-spacing: 2px;
  animation: pixelGlow 2s infinite;
}

@keyframes pixelGlow {
  0%, 100% { text-shadow: 3px 3px 0px #000, 0 0 20px #00ff41; }
  50% { text-shadow: 3px 3px 0px #000, 0 0 30px #00ff41, 0 0 40px #00ff41; }
}

.subtitle {
  font-size: 0.6em;
  opacity: 0.9;
  letter-spacing: 1px;
}

.header-buttons {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 20px;
}

.icon-btn {
  background: #00ff41;
  border: 4px solid #000;
  border-radius: 0;
  width: 50px;
  height: 50px;
  font-size: 1.5em;
  cursor: pointer;
  transition: all 0.1s ease;
  box-shadow: 4px 4px 0px #000;
  position: relative;
}

.icon-btn:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #000;
}

.icon-btn:active {
  transform: translate(4px, 4px);
  box-shadow: none;
}

.light-mode .icon-btn {
  background: #4dd0e1;
  border-color: #00796b;
  box-shadow: 4px 4px 0px #00796b;
}

.light-mode .icon-btn:hover {
  box-shadow: 2px 2px 0px #00796b;
}

/* 픽셀 배너 */
.dday-banner {
  background: #00ff41;
  border: 4px solid #000;
  padding: 15px;
  text-align: center;
  color: #000;
  font-size: 0.7em;
  margin-bottom: 15px;
  box-shadow: 6px 6px 0px #000;
  letter-spacing: 1px;
  animation: pixelBlink 1s infinite;
}

@keyframes pixelBlink {
  0%, 49% { opacity: 1; }
  50%, 100% { opacity: 0.9; }
}

.light-mode .dday-banner {
  background: #4dd0e1;
  border-color: #00796b;
  box-shadow: 6px 6px 0px #00796b;
}

/* 스트릭 배너 - 8비트 스타일 */
.streak-banner {
  background: #ff6b6b;
  border: 4px solid #000;
  padding: 15px;
  text-align: center;
  color: #fff;
  font-size: 0.7em;
  margin-bottom: 15px;
  box-shadow: 6px 6px 0px #000;
  letter-spacing: 1px;
  text-shadow: 2px 2px 0px #000;
}

/* 픽셀 정보 카드 */
.quick-info {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.quick-info-item {
  flex: 1;
  background: #1a1a2e;
  border: 4px solid #000;
  padding: 12px;
  text-align: center;
  color: #00ff41;
  font-size: 0.5em;
  box-shadow: 4px 4px 0px #000;
  letter-spacing: 1px;
}

.light-mode .quick-info-item {
  background: #fff;
  color: #00796b;
  border-color: #00796b;
  box-shadow: 4px 4px 0px #00796b;
}

.quick-info-item span {
  display: block;
  margin-bottom: 5px;
}

.quick-info-item strong {
  display: block;
  font-size: 1.4em;
}

/* 픽셀 버튼 */
.mock-exam-btn {
  width: 100%;
  padding: 20px;
  background: #ff6b6b;
  color: #fff;
  border: 4px solid #000;
  font-size: 0.7em;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 25px;
  transition: all 0.1s ease;
  box-shadow: 6px 6px 0px #000;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0px #000;
  font-family: 'Press Start 2P', cursive;
}

.mock-exam-btn:hover {
  transform: translate(3px, 3px);
  box-shadow: 3px 3px 0px #000;
}

.mock-exam-btn:active {
  transform: translate(6px, 6px);
  box-shadow: none;
}

/* 픽셀 과목 그리드 */
.subject-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 15px;
  margin-bottom: 25px;
}

.subject-card {
  background: #1a1a2e;
  border: 4px solid #000;
  padding: 25px 15px;
  text-align: center;
  cursor: pointer;
  transition: all 0.1s ease;
  box-shadow: 6px 6px 0px #000;
  position: relative;
}

.light-mode .subject-card {
  background: #fff;
}

.subject-card:hover {
  transform: translate(3px, 3px);
  box-shadow: 3px 3px 0px #000;
}

.subject-card:active {
  transform: translate(6px, 6px);
  box-shadow: none;
}

.subject-emoji {
  font-size: 3em;
  margin-bottom: 10px;
  filter: contrast(1.2) brightness(1.1);
}

.subject-name {
  font-size: 0.6em;
  font-weight: bold;
  color: #00ff41;
  margin-bottom: 8px;
  letter-spacing: 1px;
  text-shadow: 2px 2px 0px #000;
}

.light-mode .subject-name {
  color: #00796b;
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.2);
}

.subject-time {
  font-size: 0.6em;
  color: #ff6b6b;
  font-weight: bold;
  text-shadow: 1px 1px 0px #000;
}

/* 픽셀 정보 박스 */
.info-box {
  background: #1a1a2e;
  border: 4px solid #000;
  padding: 20px;
  color: #00ff41;
  text-align: center;
  box-shadow: 6px 6px 0px #000;
}

.light-mode .info-box {
  background: #fff;
  color: #00796b;
  border-color: #00796b;
  box-shadow: 6px 6px 0px #00796b;
}

.info-box p {
  margin: 8px 0;
  font-size: 0.5em;
  letter-spacing: 1px;
  line-height: 1.8;
}

/* 픽셀 모달 */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.modal-content {
  background: #1a1a2e;
  border: 6px solid #000;
  padding: 30px;
  max-width: 500px;
  width: 100%;
  box-shadow: 8px 8px 0px #000;
  animation: pixelPop 0.3s ease;
}

.light-mode .modal-content {
  background: #fff;
}

@keyframes pixelPop {
  0% { transform: scale(0); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.modal-content h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #00ff41;
  font-size: 1em;
  text-shadow: 2px 2px 0px #000;
  letter-spacing: 2px;
}

.light-mode .modal-content h2 {
  color: #00796b;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
}

.modal-content p {
  text-align: center;
  color: #00ff41;
  margin-bottom: 25px;
  font-size: 0.6em;
  letter-spacing: 1px;
  line-height: 1.8;
}

.light-mode .modal-content p {
  color: #00796b;
}

.modal-desc {
  font-size: 0.5em !important;
  opacity: 0.8;
}

/* 픽셀 휴식 버튼 */
.break-options {
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
}

.break-options button {
  flex: 1;
  padding: 15px;
  background: #00ff41;
  color: #000;
  border: 4px solid #000;
  font-size: 0.6em;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.1s ease;
  box-shadow: 4px 4px 0px #000;
  font-family: 'Press Start 2P', cursive;
  letter-spacing: 1px;
}

.break-options button:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #000;
}

.break-options button:active {
  transform: translate(4px, 4px);
  box-shadow: none;
}

.skip-btn {
  width: 100%;
  padding: 15px;
  background: #ff6b6b;
  color: #fff;
  border: 4px solid #000;
  font-size: 0.6em;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.1s ease;
  font-family: 'Press Start 2P', cursive;
  text-shadow: 2px 2px 0px #000;
  letter-spacing: 1px;
}

.skip-btn:hover {
  transform: translate(2px, 2px);
}

/* 모의고사 스케줄 */
.mock-exam-modal {
  max-width: 600px;
}

.mock-exam-schedule {
  background: #0f3460;
  border: 4px solid #000;
  padding: 20px;
  margin-bottom: 20px;
}

.light-mode .mock-exam-schedule {
  background: #e0f7fa;
}

.schedule-item {
  display: flex;
  align-items: center;
  padding: 12px 0;
  border-bottom: 2px dashed #00ff41;
}

.light-mode .schedule-item {
  border-bottom-color: #00796b;
}

.schedule-item:last-child {
  border-bottom: none;
}

.schedule-subject {
  flex: 1;
  font-size: 0.6em;
  font-weight: bold;
  color: #00ff41;
  letter-spacing: 1px;
}

.light-mode .schedule-subject {
  color: #00796b;
}

.schedule-time {
  color: #ff6b6b;
  font-weight: bold;
  margin-right: 10px;
  font-size: 0.6em;
}

.schedule-break {
  color: #00ff41;
  font-size: 0.5em;
}

.light-mode .schedule-break {
  color: #00796b;
}

.total-time {
  text-align: center;
  color: #00ff41;
  font-weight: bold;
  margin-bottom: 25px;
  font-size: 0.6em;
  letter-spacing: 1px;
}

.light-mode .total-time {
  color: #00796b;
}

.modal-buttons {
  display: flex;
  gap: 10px;
}

.start-mock-btn {
  flex: 1;
  padding: 15px;
  background: #00ff41;
  color: #000;
  border: 4px solid #000;
  font-size: 0.6em;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.1s ease;
  box-shadow: 4px 4px 0px #000;
  font-family: 'Press Start 2P', cursive;
  letter-spacing: 1px;
}

.start-mock-btn:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #000;
}

.cancel-btn {
  flex: 1;
  padding: 15px;
  background: #ff6b6b;
  color: #fff;
  border: 4px solid #000;
  font-size: 0.6em;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.1s ease;
  font-family: 'Press Start 2P', cursive;
  text-shadow: 2px 2px 0px #000;
  letter-spacing: 1px;
}

.cancel-btn:hover {
  transform: translate(2px, 2px);
}

/* 준비 화면 - 8비트 */
.ready-container {
  max-width: 600px;
  width: 100%;
  text-align: center;
  color: #00ff41;
  text-shadow: 3px 3px 0px #000;
}

.light-mode .ready-container {
  color: #00796b;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
}

.ready-subject {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 30px;
  letter-spacing: 2px;
  animation: pixelShake 0.5s infinite;
}

@keyframes pixelShake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-2px); }
  75% { transform: translateX(2px); }
}

.ready-tip {
  background: #1a1a2e;
  border: 4px solid #000;
  padding: 25px;
  font-size: 0.6em;
  line-height: 2;
  margin-bottom: 35px;
  box-shadow: 6px 6px 0px #000;
  letter-spacing: 1px;
}

.light-mode .ready-tip {
  background: #fff;
  color: #00796b;
  border-color: #00796b;
  box-shadow: 6px 6px 0px #00796b;
}

.ready-countdown {
  font-size: 5em;
  font-weight: bold;
  margin-bottom: 20px;
  animation: pixelPulse 1s infinite;
}

@keyframes pixelPulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.2); }
}

.ready-message {
  font-size: 0.8em;
  letter-spacing: 2px;
}

/* 타이머 화면 - 다마고치 LCD */
.timer-container {
  max-width: 600px;
  width: 100%;
  position: relative;
}

.mock-exam-progress {
  text-align: center;
  color: #00ff41;
  font-size: 0.6em;
  margin-bottom: 20px;
  padding: 12px;
  background: #1a1a2e;
  border: 4px solid #000;
  font-weight: bold;
  letter-spacing: 1px;
  text-shadow: 2px 2px 0px #000;
  box-shadow: 4px 4px 0px #000;
}

.light-mode .mock-exam-progress {
  background: #fff;
  color: #00796b;
  border-color: #00796b;
  box-shadow: 4px 4px 0px #00796b;
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.2);
}

.timer-header {
  text-align: center;
  margin-bottom: 35px;
}

.timer-header h2 {
  color: #00ff41;
  font-size: 1.5em;
  text-shadow: 3px 3px 0px #000;
  letter-spacing: 2px;
}

.light-mode .timer-header h2 {
  color: #00796b;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
}

.timer-display {
  background: #9bc995;
  border: 8px solid #000;
  padding: 50px 40px;
  text-align: center;
  margin-bottom: 30px;
  box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.3), 8px 8px 0px #000;
  transition: all 0.3s ease;
  position: relative;
}

.timer-display::before {
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  pointer-events: none;
}

.timer-display.break-mode {
  background: #4dd0e1;
}

.timer-display.warning {
  background: #ffe082;
  animation: pixelWarning 0.5s infinite;
}

.timer-display.critical {
  background: #ff8a80;
  animation: pixelDanger 0.3s infinite;
}

@keyframes pixelWarning {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.8; }
}

@keyframes pixelDanger {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

.time {
  font-size: 4em;
  font-weight: bold;
  color: #000;
  font-variant-numeric: tabular-nums;
  letter-spacing: 0.1em;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
  font-family: 'Press Start 2P', monospace;
}

.alert-text {
  margin-top: 20px;
  font-size: 0.6em;
  font-weight: bold;
  color: #000;
  letter-spacing: 1px;
}

.break-text {
  margin-top: 20px;
  font-size: 0.7em;
  font-weight: bold;
  color: #000;
  letter-spacing: 1px;
}

.critical-alert {
  animation: pixelBlink 0.5s infinite;
}

.progress-bar {
  height: 20px;
  background: #0f3460;
  border: 4px solid #000;
  overflow: hidden;
  margin-bottom: 30px;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5);
}

.light-mode .progress-bar {
  background: #b2ebf2;
}

.progress-fill {
  height: 100%;
  transition: width 0.3s ease;
  background: repeating-linear-gradient(
    90deg,
    #00ff41 0px,
    #00ff41 4px,
    #00d936 4px,
    #00d936 8px
  );
}

.timer-controls {
  display: flex;
  gap: 15px;
  justify-content: center;
}

.btn {
  padding: 15px 35px;
  font-size: 0.7em;
  font-weight: bold;
  border: 4px solid #000;
  cursor: pointer;
  transition: all 0.1s ease;
  box-shadow: 6px 6px 0px #000;
  font-family: 'Press Start 2P', cursive;
  letter-spacing: 1px;
}

.btn:hover {
  transform: translate(3px, 3px);
  box-shadow: 3px 3px 0px #000;
}

.btn:active {
  transform: translate(6px, 6px);
  box-shadow: none;
}

.btn-pause {
  background: #00ff41;
  color: #000;
}

.btn-stop {
  background: #ff6b6b;
  color: #fff;
  text-shadow: 2px 2px 0px #000;
}

.paused-overlay {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #000;
  color: #00ff41;
  padding: 40px 60px;
  border: 6px solid #00ff41;
  pointer-events: none;
  z-index: 100;
  text-align: center;
  box-shadow: 0 0 30px #00ff41;
  animation: pixelGlowBorder 1s infinite;
}

@keyframes pixelGlowBorder {
  0%, 100% { box-shadow: 0 0 30px #00ff41; }
  50% { box-shadow: 0 0 50px #00ff41, 0 0 70px #00ff41; }
}

.pause-main {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 20px;
  letter-spacing: 2px;
}

.pause-tip {
  font-size: 0.5em;
  line-height: 2;
  letter-spacing: 1px;
}

.completion-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 200;
  animation: pixelFlash 0.1s ease;
}

@keyframes pixelFlash {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.8; }
}

.completion-message {
  color: #00ff41;
  font-size: 1.5em;
  font-weight: bold;
  text-align: center;
  animation: pixelVictory 0.5s ease infinite;
  padding: 0 40px;
  text-shadow: 3px 3px 0px #000, 0 0 30px #00ff41;
  letter-spacing: 2px;
}

@keyframes pixelVictory {
  0%, 100% { transform: scale(1) rotate(0deg); }
  25% { transform: scale(1.1) rotate(-2deg); }
  75% { transform: scale(1.1) rotate(2deg); }
}

/* 체크리스트 - 픽셀 스타일 */
.checklist-header, .notes-header, .stats-header, .settings-header {
  margin-bottom: 30px;
}

.checklist-progress {
  text-align: center;
  color: #00ff41;
  font-size: 0.6em;
  margin-top: 12px;
  letter-spacing: 1px;
}

.light-mode .checklist-progress {
  color: #00796b;
}

.checklist-input-box {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
}

.checklist-input-box input {
  flex: 1;
  padding: 15px 20px;
  border: 4px solid #000;
  font-size: 14px;
  outline: none;
  background: #1a1a2e;
  color: #00ff41;
  font-family: 'Press Start 2P', cursive;
}

.light-mode .checklist-input-box input {
  background: #fff;
  color: #00796b;
  border-color: #00796b;
}

.checklist-input-box input::placeholder {
  color: #00ff41;
  opacity: 0.5;
}

.light-mode .checklist-input-box input::placeholder {
  color: #00796b;
}

.checklist-input-box button {
  padding: 15px 25px;
  background: #00ff41;
  color: #000;
  border: 4px solid #000;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.1s ease;
  box-shadow: 4px 4px 0px #000;
  font-family: 'Press Start 2P', cursive;
}

.checklist-input-box button:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #000;
}

.checklist-list {
  background: #1a1a2e;
  border: 4px solid #000;
  padding: 30px;
  box-shadow: 6px 6px 0px #000;
  min-height: 300px;
}

.light-mode .checklist-list {
  background: #fff;
  border-color: #00796b;
  box-shadow: 6px 6px 0px #00796b;
}

.empty-message {
  text-align: center;
  color: #00ff41;
  font-size: 0.6em;
  padding: 60px 20px;
  letter-spacing: 1px;
  line-height: 2;
}

.light-mode .empty-message {
  color: #00796b;
}

.checklist-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 0;
  border-bottom: 2px dashed #00ff41;
}

.light-mode .checklist-item {
  border-bottom-color: #00796b;
}

.checklist-item:last-child {
  border-bottom: none;
}

.checklist-content {
  display: flex;
  align-items: center;
  gap: 15px;
  flex: 1;
  cursor: pointer;
  user-select: none;
}

.checkbox {
  width: 24px;
  height: 24px;
  border: 4px solid #00ff41;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #000;
  transition: all 0.1s;
  background: #000;
}

.light-mode .checkbox {
  border-color: #00796b;
}

.checkbox.checked {
  background: #00ff41;
  border-color: #00ff41;
}

.light-mode .checkbox.checked {
  background: #4dd0e1;
  border-color: #00796b;
}

.checklist-content span {
  font-size: 0.6em;
  color: #00ff41;
  transition: all 0.3s;
  letter-spacing: 1px;
}

.light-mode .checklist-content span {
  color: #00796b;
}

.checklist-content span.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

.delete-btn {
  background: #ff6b6b;
  border: 2px solid #000;
  font-size: 20px;
  cursor: pointer;
  padding: 5px 10px;
  transition: all 0.1s;
  color: #000;
}

.delete-btn:hover {
  transform: scale(1.2);
}

/* 노트 */
.notes-tabs {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-bottom: 20px;
  justify-content: center;
}

.note-tab {
  padding: 12px 20px;
  background: #1a1a2e;
  border: 4px solid #000;
  color: #00ff41;
  font-size: 0.5em;
  cursor: pointer;
  transition: all 0.1s ease;
  box-shadow: 4px 4px 0px #000;
  font-family: 'Press Start 2P', cursive;
  letter-spacing: 1px;
}

.light-mode .note-tab {
  background: #fff;
  color: #00796b;
  border-color: #00796b;
  box-shadow: 4px 4px 0px #00796b;
}

.note-tab.active {
  background: #00ff41;
  color: #000;
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #000;
}

.note-tab:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #000;
}

.notes-editor {
  background: #1a1a2e;
  border: 4px solid #000;
  padding: 30px;
  box-shadow: 6px 6px 0px #000;
}

.light-mode .notes-editor {
  background: #fff;
  border-color: #00796b;
  box-shadow: 6px 6px 0px #00796b;
}

.notes-editor textarea {
  width: 100%;
  min-height: 400px;
  padding: 20px;
  border: 4px solid #000;
  font-size: 14px;
  font-family: 'Press Start 2P', cursive;
  resize: vertical;
  outline: none;
  background: #0f3460;
  color: #00ff41;
  line-height: 2;
}

.light-mode .notes-editor textarea {
  background: #fff;
  color: #00796b;
  border-color: #00796b;
}

.save-note-btn {
  width: 100%;
  margin-top: 15px;
  padding: 15px;
  background: #00ff41;
  color: #000;
  border: 4px solid #000;
  font-size: 0.7em;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.1s ease;
  box-shadow: 4px 4px 0px #000;
  font-family: 'Press Start 2P', cursive;
  letter-spacing: 1px;
}

.save-note-btn:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #000;
}

/* 통계 */
.back-btn {
  background: #00ff41;
  border: 4px solid #000;
  color: #000;
  padding: 12px 25px;
  font-size: 0.6em;
  cursor: pointer;
  margin-bottom: 20px;
  transition: all 0.1s ease;
  box-shadow: 4px 4px 0px #000;
  font-family: 'Press Start 2P', cursive;
  letter-spacing: 1px;
}

.back-btn:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #000;
}

.stats-header h1, .settings-header h1, .checklist-header h1, .notes-header h1 {
  color: #00ff41;
  text-align: center;
  font-size: 1.5em;
  margin-bottom: 20px;
  text-shadow: 3px 3px 0px #000;
  letter-spacing: 2px;
}

.light-mode .stats-header h1,
.light-mode .settings-header h1,
.light-mode .checklist-header h1,
.light-mode .notes-header h1 {
  color: #00796b;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
}

.stats-summary {
  background: #1a1a2e;
  border: 4px solid #000;
  padding: 35px;
  margin-bottom: 30px;
  box-shadow: 6px 6px 0px #000;
}

.light-mode .stats-summary {
  background: #fff;
  border-color: #00796b;
  box-shadow: 6px 6px 0px #00796b;
}

.total-time {
  text-align: center;
}

.total-time .label {
  font-size: 0.6em;
  color: #00ff41;
  margin-bottom: 10px;
  letter-spacing: 1px;
}

.light-mode .total-time .label {
  color: #00796b;
}

.total-time .value {
  font-size: 2em;
  font-weight: bold;
  color: #00ff41;
  text-shadow: 2px 2px 0px #000;
  letter-spacing: 2px;
}

.light-mode .total-time .value {
  color: #00796b;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
}

.weekly-stats {
  background: #1a1a2e;
  border: 4px solid #000;
  padding: 30px;
  margin-bottom: 30px;
  box-shadow: 6px 6px 0px #000;
}

.light-mode .weekly-stats {
  background: #fff;
  border-color: #00796b;
  box-shadow: 6px 6px 0px #00796b;
}

.weekly-stats h2 {
  margin-bottom: 20px;
  color: #00ff41;
  font-size: 0.8em;
  letter-spacing: 1px;
}

.light-mode .weekly-stats h2 {
  color: #00796b;
}

.weekly-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.weekly-item {
  flex: 1;
  text-align: center;
}

.weekly-item span {
  display: block;
  color: #00ff41;
  font-size: 0.5em;
  margin-bottom: 5px;
  letter-spacing: 1px;
}

.light-mode .weekly-item span {
  color: #00796b;
}

.weekly-item strong {
  display: block;
  color: #00ff41;
  font-size: 0.7em;
  letter-spacing: 1px;
}

.light-mode .weekly-item strong {
  color: #00796b;
}

.progress-bar-container {
  text-align: center;
}

.progress-bar-bg {
  height: 30px;
  background: #0f3460;
  border: 4px solid #000;
  overflow: hidden;
  margin-bottom: 10px;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5);
}

.light-mode .progress-bar-bg {
  background: #b2ebf2;
}

.progress-bar-fill {
  height: 100%;
  background: repeating-linear-gradient(
    90deg,
    #00ff41 0px,
    #00ff41 4px,
    #00d936 4px,
    #00d936 8px
  );
  transition: width 0.5s ease;
}

.progress-text {
  color: #00ff41;
  font-weight: bold;
  font-size: 0.6em;
  letter-spacing: 1px;
}

.light-mode .progress-text {
  color: #00796b;
}

.stats-list {
  background: #1a1a2e;
  border: 4px solid #000;
  padding: 30px;
  margin-bottom: 20px;
  box-shadow: 6px 6px 0px #000;
}

.light-mode .stats-list {
  background: #fff;
  border-color: #00796b;
  box-shadow: 6px 6px 0px #00796b;
}

.stats-list h2 {
  margin-bottom: 20px;
  color: #00ff41;
  font-size: 0.8em;
  letter-spacing: 1px;
}

.light-mode .stats-list h2 {
  color: #00796b;
}

.stat-item {
  margin-bottom: 20px;
}

.stat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.stat-subject {
  font-size: 0.6em;
  font-weight: bold;
  color: #00ff41;
  letter-spacing: 1px;
}

.light-mode .stat-subject {
  color: #00796b;
}

.stat-time {
  font-size: 0.6em;
  color: #ff6b6b;
  white-space: nowrap;
  letter-spacing: 1px;
}

.stat-bar {
  height: 12px;
  background: #0f3460;
  border: 2px solid #000;
  overflow: hidden;
}

.light-mode .stat-bar {
  background: #b2ebf2;
}

.stat-fill {
  height: 100%;
  transition: width 0.5s ease;
  background: repeating-linear-gradient(
    90deg,
    currentColor 0px,
    currentColor 4px,
    transparent 4px,
    transparent 8px
  );
}

.clear-stats-btn {
  width: 100%;
  padding: 15px;
  background: #ff6b6b;
  color: #fff;
  border: 4px solid #000;
  font-size: 0.7em;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.1s ease;
  box-shadow: 6px 6px 0px #000;
  font-family: 'Press Start 2P', cursive;
  text-shadow: 2px 2px 0px #000;
  letter-spacing: 1px;
}

.clear-stats-btn:hover {
  transform: translate(3px, 3px);
  box-shadow: 3px 3px 0px #000;
}

/* 설정 */
.settings-list {
  background: #1a1a2e;
  border: 4px solid #000;
  padding: 30px;
  box-shadow: 6px 6px 0px #000;
}

.light-mode .settings-list {
  background: #fff;
  border-color: #00796b;
  box-shadow: 6px 6px 0px #00796b;
}

.setting-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-bottom: 2px dashed #00ff41;
}

.light-mode .setting-item {
  border-bottom-color: #00796b;
}

.setting-item:last-child {
  border-bottom: none;
}

.setting-info {
  flex: 1;
}

.setting-title {
  font-size: 0.6em;
  font-weight: bold;
  color: #00ff41;
  margin-bottom: 5px;
  letter-spacing: 1px;
}

.light-mode .setting-title {
  color: #00796b;
}

.setting-desc {
  font-size: 0.5em;
  color: #00ff41;
  opacity: 0.7;
  letter-spacing: 1px;
}

.light-mode .setting-desc {
  color: #00796b;
}

/* 토글 */
.toggle {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 30px;
}

.toggle input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #0f3460;
  transition: .2s;
  border: 4px solid #000;
}

.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 4px;
  bottom: 4px;
  background-color: #ff6b6b;
  transition: .2s;
  border: 2px solid #000;
}

input:checked + .slider {
  background: #00ff41;
}

input:checked + .slider:before {
  transform: translateX(26px);
  background-color: #000;
}

.date-input, .number-input {
  padding: 10px 15px;
  border: 4px solid #000;
  font-size: 0.6em;
  outline: none;
  background: #1a1a2e;
  color: #00ff41;
  font-family: 'Press Start 2P', cursive;
}

.light-mode .date-input,
.light-mode .number-input {
  background: #fff;
  color: #00796b;
  border-color: #00796b;
}

.number-input {
  width: 100px;
  text-align: center;
}

/* 집중 모드 */
.focus-mode-item {
  border-top: 4px solid #ff6b6b !important;
  padding-top: 20px !important;
  margin-top: 10px !important;
}

.App.focus-mode .mock-exam-progress {
  display: none;
}

.App.focus-mode .progress-bar {
  display: none;
}

.App.focus-mode .alert-text {
  display: none;
}

.App.focus-mode .timer-header h2 {
  font-size: 2em;
}

.App.focus-mode .timer-display {
  padding: 100px 40px;
}

.App.focus-mode .time {
  font-size: 5em;
}

/* 반응형 */
@media (max-width: 768px) {
  .header h1 {
    font-size: 1.5em;
  }

  .subject-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .time {
    font-size: 3em;
  }

  .timer-header h2 {
    font-size: 1.2em;
  }

  .btn {
    padding: 12px 25px;
    font-size: 0.6em;
  }

  .ready-countdown {
    font-size: 4em;
  }
}

@media (max-width: 480px) {
  .header h1 {
    font-size: 1.2em;
  }

  .subject-grid {
    grid-template-columns: 1fr;
  }

  .time {
    font-size: 2.5em;
  }

  .timer-controls {
    flex-direction: column;
  }

  .btn {
    width: 100%;
  }
}